.Pagination {
  width: 100%;
  text-align: center;
}
.Button {
  font-size: 18px;
  font-weight: 500;
  border: none;
  background-color: inherit;
  margin: 0 5px;
  color: #9c9c9c;
  cursor: pointer;
}
.Active {
  color: #00C126;
  font-weight: 800;
}
.Disabled {
  opacity: 0.2;
}