.CounselInfo {
  padding: 10px 15px;
  box-sizing: border-box;
  border: 1px solid #E0E0E0;
  border-radius: 12px;
  display: flex;
  margin: 35px 0;
  justify-content: space-between;
  align-items: center;
}
.CounselInfo .CounselInfoPhone {
  font-size: 16px;
  color: #989898;
  font-weight: 600;
}
.CounselInfo .CounselInfoPhone > span {
  font-size: 14px;
  color: #989898;
  font-weight: 400;
}
.CounselInfo .CounselInfoCreateAt {
  font-size: 16px;
  color: #00C126;
  font-weight: 700;
}